@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  @apply p-0
  m-0
  box-border;
}

.box {
  @apply rounded-md
  border-gray-400
  p-10
  bg-hard-white
  border;
}

.expandable-container {
  @apply max-w-700
    mx-auto
    mb-50
    px-15;

  &--title {
    @apply text-30 font-bold font-roboto text-center mb-20;
  }

  &--text {
    @apply mb-15;
  }
}
